import { ErrorHandler } from './../../../handlers/errorHandler';
import { DataStorageProvider } from './../../../providers/data-storage-provider';
import { APP_DATE_FORMATS } from './../../../utils/adapters';
import { APPCONFIG } from './../../config';
import { Global } from './../../../variables/Global';
import { EEToast } from './../../../providers/ee-toast-provider';
import { EELoading } from './../../../providers/ee-loading-provider';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthenticationProvider } from './../../../providers/authentication.provider';
import { Router } from '@angular/router';
import { Component } from '@angular/core';
import { EEValidators } from './../../../utils/eevalidators';

@Component({
  selector: 'my-page-login',
  styles: [],
  template: require('./login.component.html')
})

export class PageLoginComponent {
  loginForm: FormGroup;
  constructor(
    private router: Router,
    private authenticateProvider: AuthenticationProvider,
    private eeloading: EELoading,
    private eetoast: EEToast,
    private errorHandler: ErrorHandler,
    private formBuilder: FormBuilder,
    private dataStg: DataStorageProvider) {

  }

  ngOnInit() {
    this.dataStg.deleteValue('access_token');
    this.dataStg.deleteAll();
    this.buildForm();
  }

  buildForm() {
    this.loginForm = this.formBuilder.group({
      username: this.formBuilder.control(null, [Validators.required]),
      password: this.formBuilder.control(null, [Validators.required])
    });
  }

  onSubmit() {
    this.eeloading.open();
    this.authenticateProvider.login(this.loginForm.value)
      .subscribe(
      (data: any) => {
        this.authenticateProvider.loadResidencias(this.loginForm.value.username)
          .subscribe(
          (resData: any) => {
            APPCONFIG.rol = data['rol'];
            this.dataStg.setValue('rol', data['rol']);
            APPCONFIG.user = data['userName'];
            if (resData) {
              this.dataStg.setValue("residencias", JSON.stringify(resData));
              let residenciaObj = {
                nombre: resData[0].nombre,
                id: resData[0].residenciaID
              }
              this.dataStg.setValue("residenciaObj", JSON.stringify(residenciaObj));
              APPCONFIG.brand.id = residenciaObj.id;
              APPCONFIG.brand.nombre = residenciaObj.nombre;
            }
            APPCONFIG.userImage = 'assets/images/ee-ger/us_' + data['userName'] + '.png';
            this.dataStg.setValue('access_token', data['access_token']);
            this.dataStg.setValue('residencia', APPCONFIG.brand.id);
            this.dataStg.setValue('username', this.loginForm.value.username);
            this.router.navigate(['/app/dashboard']);
            this.eeloading.close();
          },
          error => {
            this.eeloading.close();
            this.errorHandler.handleError(error);
          })
      },
      error => {
        this.eeloading.close();
        if (error && error.body && error.body.description) {
          this.errorHandler.handleError(error);
        } else {
          let _error = {
            body: {
              description: "Usuario y/o contraseña incorrecto/s"
            }
          };
          this.errorHandler.handleError(_error);
        };
      })
  }

  removeAccentsXXX(value) {
    return value.replace(/á/g, 'a')
      .replace(/é/g, 'e')
      .replace(/í/g, 'i')
      .replace(/ó/g, 'o')
      .replace(/ú/g, 'u')
  }

}



